// styles go here
* {
  box-sizing: border-box;
}

html {
  font-family: 'Roboto', sans-serif;
  background-color: antiquewhite;
}

.App {
  text-align: center;

  &__list {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(2,1fr);
    justify-items: center;
    
  }
}